<template>
  <div style="width:300px; margin: auto;">
    <b-form-input v-model="username" placeholder="username" id="input_text"></b-form-input>
    <b-form-input v-model="password" placeholder="password" id="input_text"></b-form-input>
    <b-button v-on:click="logIn" variant="primary">Log In</b-button>
    <br>
    <Alert ref="Alert" style="margin:auto;"/>
  </div>
</template>

<script>
import {onLogin} from './../apollo.js'
import {reskip} from './../functions/reskip.js'
import {USERNAME_EXISTS, LOGIN, ME} from './../constants/graphgl.js'
import Alert from './my_lib/Alert.vue'

export default {
  name: 'Login',
  components: {
    Alert,
  },
  data: function() {
    return {

      username: '',
      password: '',

    }
  },
  apollo: {
    login: {
      query: LOGIN,
      skip: true,
      variables: {
        username: '',
        password: '',
      },
    },
    usernameExists: {
      query: USERNAME_EXISTS,
      skip: true,
    },
    me: {
      query: ME,
      skip: true,
    }
  },
  methods: {
    async checkIfExistUser(username){
      reskip(this.$apollo.queries.usernameExists, {username})

      try{
        const resultIfExist = await this.$apollo.queries.usernameExists.refetch({username})
        return resultIfExist.data.usernameExists
      }catch(e){
        this.$refs.Alert.showAlert(`${e.message}`)
      }
    },

    async logIn(){
      const username = this.username
      const password = this.password

      const ifExist = await this.checkIfExistUser(username);

      if(typeof(ifExist) != "undefined" ){
        if(ifExist){
          reskip(this.$apollo.queries.login, {username, password})
          reskip(this.$apollo.queries.me, {})

          try{
              const resultLogin = await this.$apollo.queries.login.refetch({username, password})  
              const jwt = resultLogin.data.login

              await onLogin(this.$apollo.provider.defaultClient, jwt)

              const resultMe = await this.$apollo.queries.me.refetch();
              localStorage.setItem('user', JSON.stringify(resultMe.data.me))
              this.$refs.Alert.showAlert(`${username}, welcome! `)
              this.$router.push('rooms')
          }catch(e){
              this.$refs.Alert.showAlert(`${e.message}`)
          }

        }else{
              this.$refs.Alert.showAlert(`user "${this.username}" does not exist`)
        }
      }
    },
  },
}
</script>