<template>
    <b-alert
      :show="dismissCountDown"
      dismissible
      variant="info"
      @dismissed="dismissCountDown=0"
      @dismiss-count-down="countDownChanged"
    >
    {{text}}
      <b-progress
        variant="info"
        :max="dismissSecs"
        :value="dismissCountDown"
        height="4px"
      ></b-progress>
    </b-alert>
</template>

<script>

export default {
    name: 'Alert',
    data() {
      return {
        dismissSecs: 10,
        dismissCountDown: 0,
        text: '',
      }
    },
    methods: {
      countDownChanged(dismissCountDown) {
        this.dismissCountDown = dismissCountDown
      },
      showAlert(text) {
        this.text = text
        this.dismissCountDown = this.dismissSecs
      }
    }
  }
  
</script>