import gql from 'graphql-tag'

export const REGISTER = gql`query register ($username: String!, $password: String!){
    user: register(username: $username, password: $password) {
        id
        username
        timestamp
    }
   }`;

export const LOGIN = gql`query login($username: String!, $password: String!){
    login: login(username: $username, password: $password)
  }`
   
export const USERNAME_EXISTS = gql`query usernameExists($username: String!){
    usernameExists(username: $username)
  }`
   
export const ROOMS = gql`query rooms{
  rooms{
    id
    name
    owner{
      id
      username
    }
  }
  }`

export const ME = gql`query me{
  me{
    username
    id
    currentRoom{
      id
      name
      lastMessages{
        id
        text
        author {
          id
          username
        }
        timestamp
      }
      owner{
        id
        username
      }
      members{
        id
        username
      }
    }
  }
}`

export const CREATE_ROOM = gql`mutation createRoom($roomname: String!) {
  createRoom(name: $roomname) {
    id
    name
    timestamp
  }
}`
export const UPDATE_ROOM = gql`mutation updateRoom($id: ID!, $new_name: String!) {
  updateRoom(id: $id, name: $new_name) {
    id 
    name
    timestamp
  }
}`
export const DELETE_ROOM = gql`mutation deleteRoom($id: ID!) {
  deleteRoom(id: $id) {
    id 
    name
    timestamp
  }
}`

export const SUB_ROOM_CREATED = gql`subscription roomCreated {
  roomCreated {
    id name owner {id username}
    lastMessages{
      id
      text
      author {
        username
      }
      timestamp
    }
  }
}`
export const SUB_ROOM_UPDATED = gql`subscription roomUpdated {
  roomUpdated {
    id name owner {id username}     
    lastMessages{
      text
      author {
        username
      }
      timestamp
    }
  }
}`
export const SUB_ROOM_DELETED = gql`subscription roomDeleted {
  roomDeleted {
    id name owner {id username}
    lastMessages{
      text
      author {
        username
      }
      timestamp
    }
  }
}`

export const JOIN_ROOM = gql`mutation join($id: ID!) {
  joinRoom(roomId: $id) {
    id
    name
    lastMessages{
      id
      text
      author {
        id
        username
      }
      timestamp
    }
    members{
      id
      username
    }
    owner{
      id
      username
    }
  }
}`

export const LEAVE_ROOM = gql`mutation leave {
  leaveCurrentRoom {
    id 
    name
  }
}`
export const CREATE_MESSAGE = gql`mutation createMessage($text: String!) {
  createMessage(text: $text) {
    id
    timestamp
    author { id username }
    text
  }
}`

export const MEMBER_JOINED = gql`subscription memberJoined {
  memberJoined { id username }
}`
 
export const MEMBER_LEFT = gql`subscription memberLeft {
  memberLeft { id username }
}`

export const MESSAGE_CREATED = gql`subscription messageCreated {
  messageCreated {
    id
    timestamp
    author { id username }
    text
  }
}`
